import "./styles.css";
import React, { useState } from "react";

export default function App() {
  const [url, setUrl] = useState("");
  const [images, setImages] = useState([
    "https://www.simplicity.com/globalassets/catalogs/simplicity/2105n/n6700/n6700_01.jpg"
  ]);

  const addUrl = (event) => {
    alert("An image was submitted");
    setImages([...images, url]);
  };

  return (
    <div className="App">
      <h1>Pintrea Sewing app ðŸ‘µ</h1>
      <h4>Put all of your sewing inspirations here!</h4>
      <input value={url} onChange={(event) => setUrl(event.target.value)} />
      <button onClick={addUrl}>submit</button>
      <br />
      {images.map((image, index) => {
        return (
          <>
            <img
              src={image}
              alt={`pattern ${index}`}
              style={{ width: "300px" }}
            />
          </>
        );
        // or <img
      })}
    </div>
  );
}
